.title {
    display: flex;
    justify-content: center;
    font-size: 31px;
    font-weight: 600;
    margin: 0 0 15px 0;
    color: #545454;
    text-transform: capitalize;
    text-shadow: none;
    font-family: "Open Sans","HelveticaNeue","Helvetica Neue",Helvetica,Arial,sans-serif;
    line-height: 1.2;
}

.form {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.loginBtn {
    font-size: 15px;
    text-transform: capitalize;
    font-weight: 600;
    color: #fff;
    border-radius: 5px;
    outline: none;
    border: #1a73e8;
    text-shadow: none;
    padding: 15px 0;
    transition: all .08s ease 0s;
    cursor: pointer;
    min-width: 526px;
    background-color: #1a73e8;
}

.loginBtn:hover {
    opacity: 0.7;
}

.checkBoxWrap {
    display: flex;
    /*min-width: 526px;*/
    margin-bottom: 15px;
}

input[type=checkbox] {
    position: relative;
    cursor: pointer;
    width: 0;
    height: 0;
    margin: 0;
}

input[type=checkbox]:before {
    content: "";
    display: block;
    position: absolute;
    width: 16px;
    height: 16px;
    top: -12px;
    left: -15px;
    border: 2px solid #777D74;
    border-radius: 3px;
    background-color: white;
}
input[type=checkbox]:checked:after {
    content: "";
    display: block;
    width: 5px;
    height: 10px;
    border: solid #777D74;
    border-width: 0 2px 2px 0;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
    position: absolute;
    top: -11px;
    left: -8px;
}

.remember {
    position: absolute;
    width: 171px;
    text-align: center;
}

.box {
    padding-top: 50px;
}

.captchaImg {
    width: 140px;
    height: 60px;
}