.dialogs__wrapper {
    display: flex;
}

.message__list{
    margin: 0;
    padding: 10px;
    list-style: none;
    display: flex;
}

.message__item {
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding: 20px;
    text-decoration: none;
    cursor: pointer;
}

.message {
    width: 30%;
}

.text {
    padding: .75rem 1rem;
    margin: 0 1.33rem 1.33rem .3rem;
    clear: both;
    word-break: break-word;
    color: white;
    background: #50b5ff;
    border-radius: 15px;
    box-shadow: 0 2px 4px 0 rgb(80, 181, 255);
}